import React from 'react';
import ReactGA from 'react-ga';

export interface EventType extends ReactGA.EventArgs {
  withConversion?: boolean,
  isCustom?: boolean,
}

export interface WithAnalyticsProps {
  sendAnalytics: (event: EventType) => void,
}

type WithoutAnalytics<T extends WithAnalyticsProps> = Pick<T, Exclude<keyof T, 'sendAnalytics'>>;

const analyticsHoc = (sendAnalytics: WithAnalyticsProps['sendAnalytics']) =>
  function <P extends WithAnalyticsProps>(WrappedComponent: React.ComponentType<P>) {
    return class extends React.PureComponent<WithoutAnalytics<P>> {
      render() {
        return (
          <WrappedComponent
            {...this.props as any}
            sendAnalytics={sendAnalytics}
          />
        );
      }
    };
  };

export default analyticsHoc;
