import React, {useState, useEffect} from 'react';
import s from './Vacancy.module.css';
import Features from 'ui/Sections/Features';
import Steps from 'ui/Sections/Steps';
import VacancyBlock from './VacancyBlock';
import cities, {CityKey} from 'data/cities';
import _ from 'lodash';
import InputElement from 'react-input-mask';
import {withAnalytics} from 'analytics';
import {WithAnalyticsProps} from 'analytics/analyticsHOC';

import offering from 'images/offering.jpg';
import requirements from 'images/requirements.jpg';
import vacancyGirl from 'images/vacancyGirl.png';
import Button from 'ui/Button';
import {Link} from "react-router-dom";

interface Props {
  id: string,
}

const Vacancy: React.FC<Props & WithAnalyticsProps> = (props) => {
  useEffect(() => {
    props.sendAnalytics({
      category: 'Registration',
      action: 'FormLoaded',
      isCustom: true,
    });
  }, []);
  const [errors, setError] = useState({
    firstNameError: '',
    lastNameError: '',
    mobilePhoneError: '',
    emailAddressError: '',
    birthDateError: '',
    formError: '',
  });

  const [formValues, setValues] = useState({
    firstName: '',
    lastName: '',
    mobilePhone: '',
    emailAddress: '',
    birthDate: '',
    policyCheck: false,
  });

  const [formLoading, setFormLoading] = useState(false);

  const handleChecked = () => {
    setValues({
      ...formValues,
      policyCheck: !formValues.policyCheck
    })
  };

  const updateField = (e: React.FormEvent<HTMLInputElement>) => {
    const {value, name} = e.currentTarget;
    setValues({
      ...formValues,
      [name]: value
    });
    switch (name) {
      case 'firstName':
      case 'lastName':
        setError({
          ...errors,
          [`${name}Error`]: !value ? 'Поле обязательно к заполнению' : '',
        });
        break;
      case 'emailAddress':
        setError({
          ...errors,
          emailAddressError: value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) ?
            '' : 'Введите корректный Email'
        });
        break;
      case 'mobilePhone':
        setError({
          ...errors,
          mobilePhoneError: value.match(/^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){11}(\s*)?$/) ?
            '' : 'Введите корректный номер телефона'
        });
        break;
      case 'birthDate':
        setError({
          ...errors,
          birthDateError: value.match(/(0[1-9]|[12][0-9]|3[01])[- /.](0[1-9]|1[012])[- /.](19|20)\d\d/) ?
            '' : 'Введите корректную дату рождения'
        });
        break;
    }
  };

  const reformDate = () => {
    const newDate = formValues.birthDate.split('/');
    return newDate[2] + '-' + newDate[1] + '-' + newDate[0];
  };


  const handleSubmitForm = () => {
    const formData = new FormData();
    const payload = {
      sourceId: '38756',
      vacancyId: props.id,
      FirstName: formValues.firstName,
      LastName: formValues.lastName,
      MobilePhone: formValues.mobilePhone,
      EmailAddress: formValues.emailAddress,
      BirthDate: reformDate(),
    };
    formData.append('candidateRequestPublic', JSON.stringify(payload));

    props.sendAnalytics({
      category: 'Registration',
      action: 'SubmitApplication',
      withConversion: true,
    });
    setFormLoading(true);
    fetch(`https://public.skillaz.co/api/v1/candidates/requests/${props.id}`, {
      method: 'POST',
      body: formData,
    })
      .then((resp) => {
        setFormLoading(false);
        if (!resp.ok) {
          setError({
            ...errors,
            formError: 'Ошибка отправки формы',
          });
        }
        resp.json().then((result) => {
          if (result.InterviewUrl) {
            window.location.href = result.InterviewUrl;
          }
        })
      })
      .catch((e) => {
        setFormLoading(false);
        setError({
          ...errors,
          formError: 'Ошибка отправки формы',
        });
      });
  };

  const renderVacancyBlock = () => {
    const vacancies = _.flatMap((_.keys(cities) as CityKey[]), city => cities[city].vacancies);
    const vacancy = _.find(vacancies, v => v.id === props.id);
    if (!vacancy) {
      return null;
    }

    return (
      <VacancyBlock
        title={vacancy.title}
        desc={vacancy.desc}
        schedule={vacancy.schedule}
        salary={vacancy.salary}
        image={vacancy.banner}
        color={vacancy.color}
        position={vacancy.position}
      />
    );
  };

  const renderError = (error: string) => {
    if (!error) return null;
    return (
      <div className={s.error}>
        {error}
      </div>
    );
  };

  const areFieldsValid = () => {
    const hasErrors = _.some((_.keys(errors)), (err: keyof typeof errors) => errors[err]);
    const empty = _.some(_.keys(formValues), (value: keyof typeof formValues) => !formValues[value]);
    return !hasErrors && !empty && formValues.policyCheck;
  };

  const renderPoints = () => {
    const vacancies = _.flatMap((_.keys(cities) as CityKey[]), city => cities[city].vacancies);
    const vacancy = _.find(vacancies, v => v.id === props.id);
    if (!vacancy) {
      return null;
    }
    return (
      <div className={s.points}>
        <div className={s.title}>Твои задачи</div>
        <div className={s.pointsList}>
          <div className={s.sellPoint}>
            <div className={s.subTitle}>{vacancy.tasks[0].tittle}</div>
            <div className={s.pointDescription}>
              {vacancy.tasks[0].description}
            </div>
          </div>
          <div className={s.helpPoint}>
            <div className={s.subTitle}>{vacancy.tasks[1].tittle}</div>
            <div className={s.pointDescription}>
              {vacancy.tasks[1].description}
            </div>
          </div>
          <div className={s.suggestPoint}>
            <div className={s.subTitle}>{vacancy.tasks[2].tittle}</div>
            <div className={s.pointDescription}>
              {vacancy.tasks[2].description}
            </div>
          </div>
        </div>
      </div>
    )
  };

  return (
    <div className={s.header}>
      {renderVacancyBlock()}
      <Features/>
      {renderPoints()}
      <Steps/>
      <div className={s.listContainer}>
        <div className={s.listBlock}>
          <img src={requirements} alt='requirements' className={s.listHeader}/>
          <div className={s.listContent}>
            <div className={s.listTitle}>
              Ты подходишь нам, если ты:
            </div>
            <ul className={s.listIn}>
              <li>специалист со средним профессиональным/неполным высшим образованием;</li>
              <li>владеешь программами Microsoft Office, Internet Explorer, Outlook;</li>
              <li>выдерживаешь высокий темп работы;</li>
              <li>готов работать в операционном зале в течение всего рабочего дня;</li>
              <li>доброжелательно относишься к окружающим, готов оказывать помощь;</li>
              <li>находишь общий язык с людьми разного возраста, пола и социального статуса;</li>
              <li>ориентирован на высокие результаты в продажах.</li>
            </ul>
          </div>
        </div>
        <div className={s.listBlock}>
          <img src={offering} alt='offering' className={s.listHeader}/>
          <div className={s.listContent}>
            <div className={s.listTitle}>
              Мы предлагаем:
            </div>
            <ul className={s.listOut}>
              <li>Трудоустройство согласно ТК РФ</li>
              <li>Удобный график работы</li>
              <li>Гарантированный доход плюс премиальное вознаграждение</li>
              <li>Возможность работать рядом с домом/местом учебы</li>
              <li>Регулярное корпоративное обучение</li>
              <li>ДМС, страхование от несчастных случаев и тяжелых заболеваний</li>
              <li>Материальную помощь и социальную поддержку, корпоративную пенсионную программу</li>
              <li>Льготные условия кредитования</li>
              <li>Яркую и насыщенную корпоративную жизнь</li>
            </ul>
          </div>
        </div>
      </div>
      <div className={s.form}>
        <div className={s.formControl} id="form">
          <div className={s.fromTitle}>
            Оставь заявку сейчас!
          </div>
          <div className={s.formDesc}>
            Все поля обязательны для заполнения
          </div>
          <div className={s.formBlock}>
            <div className={s.formField}>
              <input
                value={formValues.firstName}
                className={s.formInput}
                onChange={updateField}
                name="firstName"
                type="text"
                placeholder="Имя"
              />
              {renderError(errors.firstNameError)}
            </div>
            <div className={s.formField}>
              <input
                value={formValues.lastName}
                className={s.formInput}
                onChange={updateField}
                name="lastName"
                type="text"
                placeholder="Фамилия"
              />
              {renderError(errors.lastNameError)}
            </div>
            <div className={s.formField}>
              <input
                value={formValues.emailAddress}
                className={s.formInput}
                onChange={updateField}
                name="emailAddress"
                type="text"
                placeholder="Email"
              />
              {renderError(errors.emailAddressError)}
            </div>
            <div className={s.formField}>
              <InputElement
                value={formValues.mobilePhone}
                className={s.formInput}
                onChange={updateField}
                name="mobilePhone"
                type="text"
                placeholder="Номер телефона"
                mask="+7 (999) 999-99-99"
              />
              {renderError(errors.mobilePhoneError)}
            </div>
            <div className={s.formField}>
              <InputElement
                value={formValues.birthDate}
                className={s.formInput}
                onChange={updateField}
                name="birthDate"
                placeholder="Дата рождения"
                mask="99/99/9999"
              />
              {renderError(errors.birthDateError)}
              <div className={s.checkbox}>
                <input
                  type="checkbox"
                  onChange={handleChecked}
                />
                <div className={s.confirmText}>
                  Я даю
                  <Link to="/agreement" className={s.confirmText}>
                    согласие на обработку персональных данных
                  </Link>
                </div>
              </div>
            </div>
            <Button
              onClick={handleSubmitForm}
              className={s.formSubmit}
              disabled={!areFieldsValid() || formLoading}
              type="submit"
              text="Отправить заявку"
            />
            {renderError(errors.formError)}
          </div>
        </div>
        <img src={vacancyGirl} alt="vacancyGirl" className={s.vacancyGirl}/>
      </div>
    </div>
  );
};

export default withAnalytics(Vacancy);
