import React from 'react';
import s from './Button.module.css';

interface Props {
  text: string,
  id?: string,
  type?: 'button' | 'submit' | 'reset',
  className?: string,
  onClick?: () => void,
  disabled?: boolean,
}

const Button: React.FC<Props> = (props) => {
  const otherClass = props.className ? `${props.className}` : '';
  const classes = `${s.button} ${otherClass}`;
  return (
    <button disabled={props.disabled} id={props.id} className={classes} onClick={props.onClick} type={props.type ? props.type : 'button'}>
      {props.text}
    </button>
  );
};

export default Button;
