import React, {useState} from 'react';
import s from './Main.module.css';
import arrow from 'images/arrow.svg';
import banner from 'images/banner.png';
import Navigation from './Navigation';
import MobileNavigation from './MobileNavigation';
import CityNavigation from './CityNavigation';
import { Link } from 'react-router-dom';

const Header: React.FC = () => {

  const [showMobileNavigation, setMobileNavigation] = useState(false);

  const renderMobileNavigation = () => {
    if ( !showMobileNavigation ) { return null; }
    return ( <div>
      <MobileNavigation />
      <div className={s.closeButton} onClick={toggleMenu()}>x</div>
    </div>)
  };

  const toggleMenu = () => () => {
    setMobileNavigation(!showMobileNavigation)
  };

  return (
    <div className={s.header}>
      <div className={s.headerTop}>
        <Link to="/"><img src={banner} alt="sberbank" width="136px"/></Link>
        <CityNavigation />
        <img src={arrow} alt="menu" width="16px" onClick={toggleMenu()} className={s.menuButton}/>
      </div>
      { renderMobileNavigation() }
      <Navigation />
    </div>
  );
}

export default Header;
