import React from 'react';
import _ from 'lodash';
import { Link, withRouter } from 'react-router-dom';
import s from './Main.module.css';
import classNames from 'classnames';
import { Location } from 'history';
import { useCity } from './index';
import cities from 'data/cities';

// ToDo: remove any if adding elements
const navItems: any = [
  // {
  //   label: 'Истории успеха',
  //   to: '/success-stories',
  // },
  {
    label: 'Видеоинтервью',
    to: '/interview',
  },
];

type Props = {
  location: Location,
};

const MobileNavigation: React.FC<Props> = ( props: Props ) => {
  const [stateCity] = useCity();

  const renderNavItems = () => {
    return _.map(navItems, (item) => {
      const { to, label } = item;
      if (!to) {
        return null;
      }

      const classes = classNames(s.navItem, {
        [s.navItemActive]: to === props.location.pathname,
      });
      return (
        <Link key={to} to={to} className={classes}>{label}</Link>
      );
    });
  };

  const vacancyClasses = classNames(s.navItem, {
    [s.navItemActive]: props.location.pathname.includes('/vacancy'),
  });

  const renderVacancyDropdown = () => {
    const cityVacancies = cities[stateCity.id].vacancies;

    const vacancyList = _.map(cityVacancies, (vacancy) => {
      return (
        <div key={vacancy.id} className={s.dropdownItem}>
          <Link to={`/vacancy/${vacancy.id}`} onClick={()=>{}} className={s.dropdownItemLink}>{vacancy.title}</Link>
        </div>
      );
    });

    return (
      <div className={s.navDropdown}>
        {vacancyList}
      </div>
    );
  };

  return (
    <div className={s.mobileNavItems}>
      <div className={vacancyClasses}>
        Вакансии
      </div>
      {renderNavItems()}
      {renderVacancyDropdown()}
    </div>
  );
};

export default withRouter(MobileNavigation);
