import ReactDOM from 'react-dom';
import FacebookPixel from 'react-facebook-pixel';
import ReactGA from 'react-ga';
import ym from 'react-yandex-metrika';

import { GA, GA_ADWORDS, FB_PIXEL } from './constants';

import analyticsHOC, { EventType } from './analyticsHOC';
import YandexMetrika from './YandexMetrika';

const initializeAnalytics = () => {
  const isDev = process.env.NODE_ENV === 'development';
  ReactDOM.render(YandexMetrika(), document.getElementById('ym'));
  FacebookPixel.init(FB_PIXEL, {} as any, { debug: isDev, autoConfig: false });
  FacebookPixel.pageView();
  ReactGA.initialize([
    {
      trackingId: GA,
      gaOptions: { name: 'tracker1' },
    },
    {
      trackingId: GA_ADWORDS,
      gaOptions: { name: 'tracker2' },
    }], {
      debug: isDev,
      alwaysSendToDefaultTracker: false,
    });
};

export const onAnalyticsSend = (event: EventType) => {
  const { withConversion, isCustom, ...e } = event;
  ReactGA.event(e, ['tracker1']);
  if (isCustom) {
    FacebookPixel.trackCustom(e.action, undefined);
  } else {
    FacebookPixel.track(e.action, undefined);
  }
  ym('reachGoal', e.action);

  if (withConversion) {
    ReactGA.event({
      category: 'event',
      action: 'conversion',
    }, ['tracker2']);
  }
};

export const withAnalytics = analyticsHOC(onAnalyticsSend);

export default initializeAnalytics;
