import React from 'react';
import Button from 'ui/Button';

interface Props {
  className?: string,
  text: string,
  scrollTo: string,
}

const ScrollButton: React.FC<Props> = (props) => {

  const handleClick = () => {
    const elem = document.getElementById(props.scrollTo);
    if (elem !== null) {
      elem.scrollIntoView({behavior: 'smooth'});
    }
  };

  return (
    <Button className={props.className} onClick={handleClick} text={props.text}/>
  );
};

export default ScrollButton;
